import Header from './Header';
import "bootstrap/dist/css/bootstrap.min.css";
import "../style.css";

const main = {
	// marginTop: '70px',
};
const instagram = {};
const footer__item = {
	backgroundColor: 'rgba(100,100,100,0.35)',
	padding: '0.5rem 1rem',
	margin: '0.5rem auto 0 auto',
	fontWeight:'200',
	color: 'white',
	margin: 'auto',
};
const footerSolid = {
	minHeight: '100px',
	display: 'flex',
	flexDirection: 'row',
	justifyContent: 'space-around',
	backgroundColor: 'rgb(218,112,214)'
};
const footer = {
	minHeight: '100px',
	display: 'flex',
	flexDirection: 'row',
	justifyContent: 'space-around',
	backgroundColor: 'rgb(218,112,214, 0.63)'
};

const Layout = props => (
	<>
	<div>
		<Header solid={props.solid} />
		<main style={main}>{props.children}</main>
		<div style={instagram}></div>
		<div style={props.solid ? footerSolid : footer}>
			<a style={footer__item} href="https://www.instagram.com/hey_lion_art/">
				instagram
			</a>
		</div>
	</div>
	</>
);

export default Layout;
